import axios from "@/ajax/request";
import { to } from "./common";

import { setUser,getUser,getToken } from "@/utils/cookie-util";

const { githubOauthOptions } = require("@/settings.js");
const { baseOauthUrl,baseApiURL, clientId, clientSecret, redirectUri,id } = githubOauthOptions;

export function isLogin(){
  let token = getToken();
  if(!token){
    return false;
  }
  let user = getUser()
  if(!user){
    return false;
  }
  return id == user.id
}

export function getOauthUrl() {
  return `${baseOauthUrl}/authorize?client_id=${clientId}&redirect_uri=${redirectUri}&response_type=code`;
}

export async function getAccessToken(code) {
  let response = await fetch(`http://authenticate.github-oauth.1236608070512569.cn-hongkong.fc.devsapp.net/authenticate/${code}`, {
    method: 'GET',
    headers: {
        'Accept': 'application/vnd.github.v3+json',
    },
    mode:'cors'
  });
  let data = await response.json();
  if (data.error) {
    return null;
  }
  return data;
}

export async function refreshToken(code) {
  let response = await fetch(`http://authenticate.github-oauth.1236608070512569.cn-hongkong.fc.devsapp.net/refreshtoken/${code}`, {
    method: 'GET',
    headers: {
        'Accept': 'application/vnd.github.v3+json',
    },
    mode:'cors'
  });

  if (!response.ok) {
    return null;
  }
  let data = await response.json();
  return data;
}

export async function getUserInfo(access_token,time) {
  // const [err, res] = await to(
  //   axios.get(`${baseApiURL}/user`,
  //   {
  //     headers:
  //     {
  //       'Accept': 'application/vnd.github.v3+json',
  //       'Authorization': `token ${access_token}`,
  //     }
  // })
  // );
  let token = getToken()
  let header = {
    'Accept': 'application/vnd.github.v3+json',
    'Authorization': `token ${token}`
  }
  let response = await fetch(`${baseApiURL}/user`, {
    method: 'GET',
    headers: header,
    mode:'cors'
  });
  if (!response.ok) {
    return null;
  }
  let data = await response.json();
  const { avatar_url, email, id, name, login, html_url } = data;
  return setUser({ avatar_url, email, id, name, login, html_url },time);
}
