const { commentOptions } = require("@/settings.js");

const VssueUtils = {

    getAccessTokenKey: function () {
        return `Vssue.${commentOptions.name.toLowerCase()}.access_token`;
    },

    setAccessToken: function (token) {
        if (token) {
            window.localStorage.setItem(VssueUtils.getAccessTokenKey(), token);
        } else {
            window.localStorage.removeItem(VssueUtils.getAccessTokenKey());
        }
    },

    getAccessToken: function () {
        return window.localStorage.getItem(VssueUtils.getAccessTokenKey())
    },

    removeToken: function () {
        window.localStorage.removeItem(VssueUtils.getAccessTokenKey());
    }
}


export { VssueUtils }