const {owner,repo,clientId,clientSecret,proxy} = githubOauthOptions;

class MyGithubV3 extends GithubV3{
    constructor() {
        console.log(githubOauthOptions.owner)
        super({baseURL : 'https://github.com',owner, repo, labels : "labels", clientId, clientSecret, state : "state", proxy,})
    }
}


export default new MyGithubV3();