/**
 * 鉴权文件，设置白名单
 * 流程：非黑名单，通行。黑名单，验证token或RefreshToken，无则跳转申请code,获取token。
 */

import router from "./path";
import { getToken, getRefreshToken, setToken } from "@/utils/cookie-util";
import { getAccessToken, getOauthUrl, refreshToken, getUserInfo,isLogin } from "@/utils/oauth-util-github";
import MyGithubV3 from "@/utils/vssue/oauth-util-github";
const whiteList = [
  "/",
  "/article",
  "/works",
  "/about",
];
const blackList = [
  "/admin/list",
  // "/admin",
];

router.beforeEach(async (to, from, next) => {

  if (blackList.indexOf(to.path) !== -1) {
    if (to.query && to.query.code) {  //验证回调code
      const token = await getAccessToken(to.query.code);
      console.log(ss);
      if (token.access_token) {
        setToken(token);
        await getUserInfo(token.access_token,new Date(new Date() * 1 + 1000 * token.expires_in));
        if(isLogin()){
          next({ path: "/admin" });
        } else {
          if(alert("验证错误！")){
            next({ path: "/article" });
          }
        }
      }
      return
    }
    let reToken = getRefreshToken()
    if (isLogin()) {
      next();
    } else if(reToken) {
      const token = await refreshToken(reToken);
      setToken(token);
      await getUserInfo(token.access_token,new Date(new Date() * 1 + 1000 * token.expires_in));
      if(isLogin()){
        next();
      } else {
        MyGithubV3.redirectAuth()
      }
    } else {
      // window.name = location.href;
      // location.href = getOauthUrl();
      MyGithubV3.redirectAuth()
    }

  } else {
    next();
  }
});

router.afterEach(() => { });

export default router;
