import Cookies from "js-cookie";
import { VssueUtils } from "@/utils/vssue-utils"

const TOKEN_CODE = "blog_access_token";

const refresh_token = "blog_refresh_token";

export function getToken() {
  return Cookies.get(TOKEN_CODE);
}

export function getRefreshToken() {
  return Cookies.get(refresh_token);
}

export function setToken(token) {
  var millisecond = new Date().getTime();
  // VssueUtils.setAccessToken(token.access_token);
  
  let reTime = new Date(millisecond + 1000 * token.refresh_token_expires_in)
  let toTime = new Date(new Date() * 1 + 1000 * token.expires_in)

  Cookies.set(refresh_token, token.refresh_token,{
    expires: reTime,
  });
  Cookies.set(TOKEN_CODE, token.access_token, {
    expires: toTime,
  });

}

export function removeToken() {
  Cookies.remove(refresh_token);
  Cookies.remove(TOKEN_CODE);
  Cookies.remove("User");
  VssueUtils.removeToken();
}

export function getUser() {
  return JSON.parse(Cookies.get("User")||"{}");
}

export function setUser(userInfo,time) {
  return Cookies.set("User", userInfo,{
    expires: time,
  });
}
